import {
  browserLocalPersistence,
  browserSessionPersistence,
  GoogleAuthProvider,
  setPersistence,
  signInWithEmailAndPassword,
  signInWithPopup,
} from "firebase/auth"

import LoginRequest from "@/types/auth/LoginRequest"

import { auth } from "./firebase.config"

export async function loginWithEmail(req: LoginRequest) {
  try {
    const { email, password, rememberMe } = req
    await setPersistence(
      auth,
      rememberMe ? browserLocalPersistence : browserSessionPersistence
    )

    const userCredential = await signInWithEmailAndPassword(
      auth,
      email,
      password
    )
    const user = userCredential.user
    const idToken = await user.getIdToken()
    return { user, idToken }
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
  } catch (e: any) {
    if (
      e.code === "auth/invalid-credential" ||
      e.code === "auth/wrong-password"
    ) {
      throw {
        message: "Invalid email or password",
      }
    }
    if (e.code === "auth/user-not-found") {
      throw { message: "User not found" }
    }

    throw { message: "Failed to login. Please try again" }
  }
}

export default async function loginWithGoogle(rememberMe: boolean = false) {
  try {
    await setPersistence(
      auth,
      rememberMe ? browserLocalPersistence : browserSessionPersistence
    )
    const result = await signInWithPopup(auth, new GoogleAuthProvider())
    const user = result.user
    const idToken = await user.getIdToken()

    return { user, idToken }
  } catch (e) {
    if (process.env.NODE_ENV === "development") console.error(e)
    throw { message: "ƒêƒÉng nh·∫≠p b·∫±ng Google th·∫•t b·∫°i. Vui l√≤ng th·ª≠ l·∫°i." }
  }
}
